* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "fontset", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "fontset";
  src: url(./GenSekiGothic-B.ttc);
}
header,
body {
  height: 100%;
}
// 定義漸層顏色變數
$gradient-colors: #ffffff, #6dd5fa, #2980b9;

header {
  background: linear-gradient(to right, $gradient-colors);
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  div.logo {
    display: flex;
    align-items: center;
    flex: 3 1 400px;
    padding: 0.75rem 1rem;
    img.plg {
      max-width: 12%;
      height: auto;
      vertical-align: middle;
    }
    img.t1 {
      max-width: 18%;
      height: auto;
      vertical-align: middle;
    }
  }
}

nav {
  flex: 2 1 400px;
  ul {
    display: flex;
    list-style-type: none;
    justify-content: space-around;
    li {
      a {
        padding-right: 2rem;
        color: hsl(225, 49%, 18%, 50%);
        text-decoration: none;
        font-size: 1.5rem;
      }
    }
  }
}
nav ul li a.active {
  color: #ffffff;
  border-radius: 5px;
}

nav ul li a:hover {
  color: rgb(255, 255, 255, 80%);
  transition: 1s;
}

/* Dropdown styles */
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: white;
  min-width: 100px;
  z-index: 1;
  border-radius: 10px;
  top: 100%;
  a {
    color: #172243;
    padding: 1rem;
    text-decoration: none;
    display: block;
    font-size: 1rem;
    font-weight: bold;
  }
  a:hover {
    transition: 1s;
    color: #2980b9;
  }
  a.active {
    color: #2980b9;
  }
}
.dropdown:hover .dropdown-content {
  display: block;
}
